
.th-post-grid-wrapper{
    box-sizing: border-box;
    padding-top: 0px;
    padding-bottom: 0px;
    padding-left: 0px;
    padding-right: 5px;
    margin-top: 0px;
    margin-bottom: 0px;
    margin-left: 0px;
    margin-right: 5px;
    position: relative;
    z-index: 0;
    align-self: start;
    flex-basis: none;
    flex-grow: 1;
    flex-shrink: 1;
    box-shadow: none;
    background: white;
    background-position: 0.5,0.5;
    background-attachment: scroll;
    background-repeat: no-repeat;
    background-size: cover;
    transition: all 0s ease-in-out;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 37px 20px;
    grid-auto-rows: minmax(100px, auto);

}

.th-post-grid-wrapper .th-post-grid-inline{
    padding-top: 2px;
    padding-bottom: 2px;
    padding-left: 2px;
    padding-right: 2px;
    display: grid;
    background:whitesmoke ;
    border-radius: 5px;
    font-size: 16px;
    border: solid 1px #D3D3D3;
    color: #808080;
    position: relative;
    width:100%;
    overflow: hidden;
    background-attachment: scroll;
    background-repeat: no-repeat;
    background-size: cover;
}

.th-post-grid-wrapper .th-post-grid-inline .post-grid-category-styles{
    padding-top: 8px;
    padding-bottom: 8px;
    text-decoration: none;
    padding-left: 10px;
